.header {
  position: relative;
  min-height: 100vh;
  padding: 53px 15px;
  background-size: cover;
  background: linear-gradient(to top, #ffffff 50%, #4780fc 50%);
}
.header__top {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.header__social {
  min-width: 100px;
}
.header__logo {
  position: absolute;
  margin-left: 8px;
  animation: animL 2s;
  animation-duration: 2s;
  -webkit-animation: animLogo 2s;
}
@keyframes animLogo {
  from {
    left: -3999px;
  }
  to {
    left: 0;
  }
}
.header__wrap {
  position: relative;
}
.header__topic {
  position: absolute;
  height: 194px;
  top: 50%;
  transform: translate(0, -56%);
  -webkit-transform: translate(0, -56%);
  -moz-transform: translate(0, -56%);
  -ms-transform: translate(0, -56%);
  -o-transform: translate(0, -56%);
}
@media (max-width: 423px) {
  .header__topic {
    top: 50%;
    transform: translate(0, -56%);
    -webkit-transform: translate(0, -56%);
    -moz-transform: translate(0, -56%);
    -ms-transform: translate(0, -56%);
    -o-transform: translate(0, -56%);
  }
}
.header__title {
  position: absolute;
  top: 119px;
  width: 410px;
  font-family: "Open Sans", sans-serif;
  font-size: 84px;
  font-weight: 100 !important;
  color: #4780fc;
  animation: animTitle 2s;
  animation-duration: 2s;
  -webkit-animation: animTitle 2s;
}
@keyframes animTitle {
  from {
    left: 3999px;
  }
  to {
    left: 0;
  }
}
@media (max-width: 423px) {
  .header__title {
    width: 100%;
  }
}
.header__title-active {
  font-family: "Open Sans", sans-serif;
  font-size: 84px;
  font-weight: 700;
  color: #4780fc;
}
.header__subtitle {
  margin-top: 10px !important;
  margin-left: 8px !important;
  font-family: "Open Sans", sans-serif;
  font-size: 28px;
  font-weight: 300;
  color: #ffffff;
}
.social__list {
  display: flex;
  justify-content: space-between;
  list-style: none;
}

.social__img {
  width: 20px;
  height: 20px;
}
.img-svg path {
  fill: #ffffff;
}
.logo__img {
  width: 285px;
}
.header__burger-btn {
  outline: none;
  cursor: pointer;
  background: transparent;
  border: none;
}
.header__burger-img {
  width: 39px;
}
